import React, { Component } from 'react';
// import { NarrativeChart } from '../vis';
import {NarrativeChart} from 'narchart'
import './playground.css';

export default class Chart extends Component {

    constructor(props) {
        super(props);
       
        this.state = {
            chartSize:1
        };
      }

    componentDidMount() {
        let spec = this.props.spec;
        let container = "#demo-chart";
        this.vis = new NarrativeChart();
        this.vis.container(container);
        this.vis.load(spec);
        this.vis.generate();
        window.addEventListener("resize", this.resizeChart)
        this.setState({
            chartSize: (window.outerHeight-92)/770
        })
    }

    componentDidUpdate() {
        let container = "#demo-chart";
        let spec = this.props.spec;
        this.vis.container(container);
        this.vis.stop()
        this.vis.load(spec);
        this.vis.generate();
    }
    componentWillUnmount(){
        window.removeEventListener('resize', this.resizeChart)
    }
    resizeChart=()=>{
        let h = window.outerHeight;
        let size_temp = (h -92)/770;
        this.setState({
            chartSize:size_temp
        })
    }

    render() {
        const {chartSize} =this.state;
        let height = 640, width = 640;
        if (this.props.spec.chart) {
            let size = this.props.spec.chart.size;
            switch (size) {
                case 'wide':
                    height = 220;
                    width = 560;
                    break;
                case 'middle':
                    height = 200;
                    width = 360;
                    break;
                case 'small':
                    height = 150;
                    width = 235;
                    break;
                case 'unitvis':
                    height = 720;
                    width = 680;
                    break;               
                default:
                    break;
            }
        }
        return (
            <div id='frame' style={{ marginLeft: 30, marginTop: 30, height: height, width: width, borderStyle: 'solid', borderWidth: 1, borderColor: '#CCCCCD', transform:`translate(-350px,-350px) scale(${chartSize}) translate(350px,350px)` }}>
                <div id='demo-chart'
                    style={{
                        height: height,
                        width: width,
                        display: "flex",
                        flexDirection: "column",
                        alignItems: "center",
                        // justifyContent: "center"
                    }} />
            </div>
        )
    }
}
