import React from 'react';
import Chart from './Chart';
import Examples from './Examples';
import EditPannel from './EditPannel';
import SplitPane, { Pane } from 'react-split-pane';
import './font.css';
import './playground.css';
import LZString from 'lz-string'
import copy from 'copy-to-clipboard'
import {Modal, Button, message} from 'antd'
import { jsPDF } from "jspdf";
import { svgAsPngUri } from "save-svg-as-png";
import domtoimage from 'dom-to-image';
import { saveAs } from 'file-saver';
import {
  HashRouter as Router,
  Switch,
  Route,
  //useParams
} from "react-router-dom";


export default class Playground extends React.Component {
  constructor(props) {
    super(props);
    let status = 'static';
    let locationHash;
    if(window.location.hash.split("/").length>2){
      locationHash = window.location.hash.substr(6, window.location.hash.length)
    }
    let spec;
    if(locationHash){
      spec = JSON.parse(LZString.decompressFromBase64(locationHash))
    }else{
      spec = require('../spec/scatterplot.json')
    }
   
    this.state = {
      spec: spec,
      status: status,
      shareLink:"",
      shareClick: false,
      exportClick:false,
      examplesClick:false,
    };
  }
  
  onEndEdit = (spec) => {
    this.setState({
      spec
    })
  }
  changeStatus = (event) => {
    let { spec, specs } = this.state;
    // spec.chart.duration = event.target.value === 'animation' ? 4000 : 0;
    this.setState({
      status: event.target.value,
      spec: spec,
      specs: specs
    })
  }
  changeStaticSpec = (event) => {
    let spec = require('../spec/' + event.target.value + '.json');
    this.setState({
      spec
    })
  }
  changeSize = (event) => {
    let chart = Object.assign({}, this.state.spec.chart);
    let spec = Object.assign({}, this.state.spec);
    chart.size = event.target.value;
    spec.chart = chart;
    this.setState({
      spec: spec,
    })
  }
  updateChart = () => {
    try{
      if (this.state.status === 'video') {
        this.setState({
          specs: JSON.parse(document.getElementsByTagName('pre')[0].innerText)
        })
      } else {
        this.setState({
          spec: JSON.parse(document.getElementsByTagName('pre')[0].innerText)
        })
      }
    }catch (error) {
      console.log("json parse error:" + error);
  }
    
  }
/** -------------------------------Export ----------------------------------------------*/
exportClick = () => {
  this.setState({
    exportClick:true
  })
}  
exportCancle = () => {
  this.setState({
    exportClick:false
  })
}
/** -------------------------------Share ----------------------------------------------*/
  shareClick = () => {
    let jsonText = this.state.spec;
    let compressResult=LZString.compressToBase64(JSON.stringify(jsonText));
    let url="https://narchart.github.io/editor/#/url/"+compressResult
    // let url="http://localhost:3000/#/url/"+compressResult
    this.setState({
      shareClick:true,
      shareLink: url
    })
  }  
  shareCancle = () => {
    this.setState({
      shareClick:false
    })
  }
  shareLink = () => {
    copy(this.state.shareLink)
    message.success({
      content:"Copied",
      style: {
        marginTop: '62px',
      },
    })

  }
  openLink = () => {
    window.open(this.state.shareLink)

  }
  /** -------------------------------Examples ----------------------------------------------*/

  examplesClick = () => {
    this.setState({
      examplesClick:true
    })
  }  
  examplesCancle = () => {
    this.setState({
      examplesClick:false
    })
  }
  // ---------------------------------------------------------------------------------------//
  showGallary = (props) => {
    props.history.push('/gallary')
  }

  /** -------------------------------Export ----------------------------------------------*/
  covertSVG2Png = () => {
    
        // html2canvas(document.getElementById('demo-chart'),{useCORS:true}).then(function (canvas) {
        //     //
        //     var timers=new Date();
        //     var fullYear=timers.getFullYear();
        //     var month=timers.getMonth()+1;
        //     var date=timers.getDate();
        //     var randoms=Math.random()+'';
        //     //
        //     var numberFileName=fullYear+''+month+date+randoms.slice(3,10);
        //     var imgData=canvas.toDataURL();
        //     //
        //     var saveFile = function(data, filename){
        //         var save_link = document.createElementNS('http://www.w3.org/1999/xhtml', 'a');
        //         save_link.href = data;
        //         save_link.download = filename;

        //         var event = document.createEvent('MouseEvents');
        //         event.initMouseEvent('click', true, false, window, 0, 0, 0, 0, 0, false, false, false, false, 0, null);
        //         save_link.dispatchEvent(event);
        //     };
        //     //+
        //     var filename = numberFileName + '.png';
        //     saveFile(imgData,filename);
        //     //document.body.appendChild(canvas);  
        // })
    const node = document.getElementById('demo-chart')
    // let imgArr=node.querySelectorAll('image')
    // imgArr[1].src=imgArr[1].href.baseVal

    domtoimage.toBlob(node).then((blob) => {
      // console.log(" ~ file: index.js ~ line 210 ~ Playground ~ domtoimage.toBlob ~ blob", blob)
      // file-save 
      saveAs(blob, '.png')
    })
  }

  covertSVG2json = () => {
    let inputJson = JSON.stringify(this.state.spec, null, '\t')
    let fixJson = inputJson.replace(/\#/g,'%23').replace(/\?/g,'%3F').replace(/\&/g,'%26').replace(/\=/g,'%3D')
    const a = document.createElement('a');
    a.download = 'visualization.json';
    a.href = 'data:application/json;charset=utf-8,' + fixJson;
    a.click();
  }

  covertSVG2svg = () => {
    let svg = document.querySelector('svg')
    if(svg){
      let svgHtml = svg.outerHTML.replace('<svg ','<svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" version="1.1" ')
      const base64doc = btoa(decodeURIComponent(encodeURIComponent(svgHtml)));
      const a = document.createElement('a');
      const e = new MouseEvent('click');
      a.download = 'visualization.svg';
      a.href = 'data:image/svg+xml;base64,' + base64doc;
      a.dispatchEvent(e);
    }
  }
  
  covertSVG2pdf = () => {
    new Promise(async (resolve, reject) => {
    let node = document.querySelector('svg')
    if(node){
      const graph = document.querySelector('svg');
      const pdf = new jsPDF("l", "pt", [1000, 1000]);
      const pdfCanvas = document.createElement("canvas");
      pdfCanvas.setAttribute("width", 1000);
      pdfCanvas.setAttribute("height", 1000);

      const dataURI = await svgAsPngUri(graph);

      pdf.addImage(dataURI, "PNG", 0, 0);
      pdf.save("visualization.pdf");
    }
    })
  }

  covertSVG2html = () => {
    const svg = document.querySelector('svg')
    if(svg){
      let svgHtml = svg.outerHTML.replace('<svg ','<svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" version="1.1" ')
      // let wholeHtml = '<!DOCTYPE html> <html> <head> </head> <body> '+svgHtml+' </body> </html>'
      const base64doc = btoa(decodeURIComponent(encodeURIComponent(svgHtml)));
      const a = document.createElement('a');
      const e = new MouseEvent('click');
      a.download = 'visualization.html';
      a.href = 'data:image/svg+xml;base64,' + base64doc;
      a.dispatchEvent(e);
    }
  }

  // ---------------------------------------------------------------------------------------//

  render() {
    const { spec, status, shareClick,examplesClick,exportClick, shareLink } = this.state;

    let displayview;
    let shownspec;
    switch (status) {
      case 'static':
        shownspec = spec;
        displayview = <Chart spec={spec} />;
        break;
      default:
        break;
    }
    return (
      <Router>
        <Switch>
          <Route exact path="/*" render={
            props => {
              return <div style={{ height: "100%" }}>
                <div className="header">
                  Narrative Charts
                  <div className='topButton' onClick={this.updateChart} style={{marginLeft:27}}>
                      <div className='topIconRun topIcon' ></div>
                      Run
                  </div>
                  <div className='topButton'onClick={this.exportClick}>
                    <div className='topIconExport topIcon' ></div>
                      Export
                  </div>
                  <div className='topButton' onClick={this.shareClick}>
                    <div className='topIconShare topIcon' ></div>
                      Share
                  </div>
                  <div className='topButton' onClick={this.examplesClick}>
                    <div className='topIconExamples topIcon' ></div>
                      All Examples
                </div>
                <Modal
                  key = {"modal_share"}
                  className = {"share_chart"}
                  visible={shareClick}
                  // closable = {false}
                  centered
                  title={"Share"}
                  onOk={() => this.shareCancle()}
                  onCancel={() => this.shareCancle()}
                  style={{ top:-100}}
                  width={"calc(100% - 300px)"}
                  height={"245px"}
                  footer={null}
                  >  
                  <hr className="modalDivider"></hr>
                  <div className="shareContent">
                    <p className="shareURLTitle">Share via URL</p>
                    <div className="shareButtonMix">
                      <p className="shareLink">{shareLink}</p>
                      {/* <p>We pack the Narrative Charts specification as an encoded string in the URL. We use a LZ-based compression algorithm. When whitespaces are not preserved, the editor will automatically format the specification when it is loaded.</p> */}
                      <Button className="shareButton" onClick={()=>this.openLink()}>Open Link</Button>
                      <Button className="shareButton" onClick={()=>this.shareLink()}>Copy Link</Button>
                    </div>
                  </div>
              </Modal>
              <Modal
                  key = {"modal_export"}
                  className = {"export_chart"}
                  title={"Export"}
                  centered
                  visible={exportClick}
                  onOk={() => this.exportCancle()}
                  onCancel={() => this.exportCancle()}
                  style={{ top:0}}
                  width={1140}
                  footer={null}
                  >  
                  <hr className="modalDivider"></hr>
                  <div className='exportContent'>
                    <div className='exportPng'>
                      <div className='exportRow'>
                        <div className='exportImage'>
                          <div className='exportPngImage'></div>
                        </div>
                        <div className='exportDescription'>
                          <p><strong>PNG</strong></p>
                          <p>PNG is a bitmap image format which is made up of a fixed number of pixels. They have a fixed resolution and cannot be scaled.</p>
                        </div>
                        <div className='exportButtonBox'>
                          <Button className="exportButton" onClick={()=>this.covertSVG2Png()}>Download</Button>
                        </div>
                      </div>
                    </div>
                    <hr className='exportContentDivider'></hr>
                    <div className='exportJson'>
                      <div className='exportRow'>
                        <div className='exportImage'>
                          <div className='exportJsonImage'></div>
                        </div>
                        <div className='exportDescription'>
                          <p><strong>JSON</strong></p>
                          <p>JSON is a lightweight data-interchange format.</p>
                        </div>
                        <div className='exportButtonBox'>
                          <Button className="exportButton" onClick={()=>this.covertSVG2json()}>Download</Button>
                        </div>
                      </div>
                    </div>
                    <hr className='exportContentDivider'></hr>
                    <div className='exportSvg'>
                      <div className='exportRow'>
                        <div className='exportImage'>
                          <div className='exportSvgImage'></div>
                        </div>
                        <div className='exportDescription'>
                          <p><strong>SVG</strong></p>
                          <p>SVG is a vector image format which uses geometric forms to represent different parts as discrete objects and are infinitely scalable.</p>
                        </div>
                        <div className='exportButtonBox'>
                          <Button className="exportButton" onClick={()=>this.covertSVG2svg()}>Download</Button>
                        </div>
                      </div>
                    </div>
                    <hr className='exportContentDivider'></hr>
                    <div className='exportPdf'>
                      <div className='exportRow'>
                        <div className='exportImage'>
                          <div className='exportPdfImage'></div>
                        </div>
                        <div className='exportDescription'>
                          <p><strong>PDF</strong></p>
                          <p>PDF is a vector format usually used for documents.</p>
                        </div>
                        <div className='exportButtonBox'>
                          <Button className="exportButton" onClick={()=>this.covertSVG2pdf()}>Download</Button>
                        </div>
                      </div>
                    </div>
                    <hr className='exportContentDivider'></hr>
                    <div className='exportHtml'>
                      <div className='exportRow'>
                        <div className='exportImage'>
                          <div className='exportHtmlImage'></div>
                        </div>
                        <div className='exportDescription'>
                          <p><strong>HTML</strong></p>
                          <p>HTML is a document format to be displayed in a browser.</p>
                        </div>
                        <div className='exportButtonBox'>
                          <Button className="exportButton" onClick={()=>this.covertSVG2html()}>Download</Button>
                        </div>
                      </div>
                    </div>
                  </div>
                 
              </Modal>
              <Modal
                  key = {"modal_examples"}
                  className = {"examples_chart"}
                  title={"Examples"}
                  centered
                  visible={examplesClick}
                  onOk={() => this.examplesCancle()}
                  onCancel={() => this.examplesCancle()}
                  width={"calc(100% - 300px)"}
                  footer={null}
                  >  
                   <hr className="modalDivider"></hr>
                  <Examples/>
                 
              </Modal>
                
                </div>
                <div className='pannelWrapper'>
                  <SplitPane split="vertical">
                    <div className='editPannel'>
                      <EditPannel onEndEdit={this.onEndEdit} spec={shownspec} />
                    </div>
                    <div className='chartPannel'>
                      {displayview}
                     </div>
                  </SplitPane>
                </div>
               
              </div >
            }
          }>
          </Route>
        </Switch>
      </Router >
    )
  }
}

