import React, { Component } from 'react';
import './examples.css';
import LZString from 'lz-string'
import exampleData from './examplesData';

export default class Examples extends Component {
    constructor(props){
        super(props)
        this.state={
            data_temp: exampleData
        }
    }
    exampleLaunch=(specJson)=>{
        letcompressResult=LZString.compressToBase64(JSON.stringify(specJson));
        window.location.href="https://narchart.github.io/editor/#/url/"+compressResult;
        // window.location.href="http://localhost:3000/#/url/"+compressResult;
        window.location.reload()
    }  
    render() {
        const {data_temp} = this.state;
        return( 
            <div  className="charts-gallery">
                 {data_temp.map((d,index)=>{
                   let url=  require("./examplesImg/gallery"+index+".jpg");
                     return(
                        <div className="chart-gallery-grid" key={"example"+index}>
                            <div><img src={url} alt=""/></div> 
                            <div className="galary-description">
                                <div className="galary-word" title={d.title}>{d.title}</div>
                                <div className="galary-button"> 
                                    <button id="gallery-launch" onClick={()=>this.exampleLaunch(d.specJson)}>Launch</button>
                                </div>
                            </div>
                        </div>
                     )
                 })}
                </div>
        )
    }
}